import sendRequestAndGetResponse from './sendRequestAndGetResponse';

export const getUrlApp = (req?: any) => {
  // If the function is being called on the server-side
  if (req) {
    return req.headers.host;
  }

  // If the function is being called on the client-side
  if (typeof window !== 'undefined') {
    return `${window.location.protocol}//${window.location.host}`;
  }

  return '';
};

export const getUserApiMethod = () =>
  sendRequestAndGetResponse(`/get-user`, {
    method: 'GET',
  });

export const emailLoginLinkApiMethod = ({
  email,
  loginToken,
}: {
  email: string;
  loginToken?: string;
}) =>
  sendRequestAndGetResponse('/auth/email-login-link', {
    qs: { loginToken },
    body: JSON.stringify({ user: email }),
  });

export const sendApiRequest = ({ path, data, method='POST' }: { path: string, data?: any, method? }) =>
  sendRequestAndGetResponse(path, {
    method,
    body: data && JSON.stringify(data),
  });

export const uploadFileRequest = (formData) =>
  fetch(`${getUrlApp()}/upload-file`, {
    method: 'post',
    body: formData,
    credentials: 'include',
  });

export const readFile = (key: string) => {
  if (key && key.includes('http')) {
    return key;
  }

  return `${getUrlApp()}/read-file?key=${key}`
};