import React, { useEffect } from 'react';
import { useRouter } from 'next/router';
import { ThemeProvider } from '@mui/material/styles';
import CssBaseline from '@mui/material/CssBaseline';
import '../styles/global.css';
import 'react-big-calendar/lib/css/react-big-calendar.css';
import 'react-calendar/dist/Calendar.css';

import { SessionProvider } from '../components/SessionContext';

import Notifier from '../components/common/Notifier';
import Confirmer from '../components/common/Confirmer';

import { themeDark, themeLight } from '../lib/theme';

const MyApp: React.FC<{ Component: React.FC; pageProps: any }> = ({ Component, pageProps }) => {
  const isThemeDark = false;

  const router = useRouter();

  useEffect(() => {
    if (typeof window !== 'undefined') {
      const hostname = window.location.hostname;

      if (hostname.includes('signup.') && router.pathname !== '/signup') {
        router.push('/signup');
      }
    }
  }, [router]);

  return (
    <SessionProvider>
      <ThemeProvider theme={isThemeDark ? themeDark : themeLight}>
        <CssBaseline />
        <Component {...pageProps} />

        <Notifier />
        <Confirmer />
      </ThemeProvider>
    </SessionProvider>
  );
};

export default MyApp;
